.App {
  max-width: 1920px;
    margin: 0;
}

.centered {
  justify-content: center;
  align-items: center;
  display: flex;
    margin: 0;
}

.kontakt-boks{
  height: 50px;
  padding-top: 10px;
  width: 100%;
  float: right;
  background-color: #F6A95D;
  font-size: 18px;
}


.Second_picture img {
  width: 100%;
    margin: 0;
}

.Second_picture {
  width: 100%;
  height: 400px;
  background-size: cover;
  display: block;
  overflow: hidden;
    margin: 0;
}

.Main_picture img {
  width: 100%;
  max-height: auto;
  margin: 0;
}

.Main_picture{
    width: 100%;
    overflow: hidden;
    margin-bottom: -5px;
}

.Footer_picture img {
  width: 100%;
  max-height: auto;
  background-image: "./assets/web_trash.png";
  margin: 0;
}

.Footer_picture{
    width: 100%;
    overflow: hidden;
    background-image: url("./assets/web_trash.png");
    margin-bottom: -5px;
}

.left {
  float: left;
  align-items: left;
  align-items: center;
  display: flex;
  padding-left: 15%;
    margin: 0;
}

.right {
  float: right;
  align-items: right;
  align-items: center;
  display: flex;
    padding-right: 10%;
      margin: 0;
}


.icon_body {
  height: 300px;
  width: auto;
    margin: 0;
}

.icon_body img {
  height: 300px;
  width: auto;
    margin: 0;
}

.Tekstfelt_left{
  width: 100%;
  height: 300px;
  background-color: #FBD4AD;
    margin: 0;
}

.Tekstfelt_left img{
  float: right;
    margin: 0;
}

.Tekstfelt_right{
  width: 100%;
  height: 300px;
  background-color: #F6A95D;
    margin: 0;
}

.Tekstfelt_right img{
  float: left;
    margin: 0;
}
